@import "../../../src/scss/variables_drupal";

.success-stories-wrapper {
  display: flex;
  justify-content: space-between;

  .subtitle {
    @include small-text;
    text-transform: uppercase;
    color: var(--color-orange);
    margin-bottom: 10px;
    font-weight: bold;
    flex: 0 0 auto;
  }
}

.featured-story {
  display: flex;
  flex-direction: column;

  > div {
    display: flex;
    flex: 1;
  }

  .views-row {
    display: flex;
  }

  .node {
    display: flex;
    flex-direction: column;
  }
}

.success-stories {
  display: flex;
  flex-direction: column;

  h3 {
    text-align: center;
  }
}
