:root {
  --color-orange: #E87722;
  --color-orange-darker: #C4661E;
  --color-yellow: #F5CD26;
  --color-yellow-darker: #AB901B;
  --color-black: #1E1E1E;
  --color-text: #1E1E1E;
  --color-white: #FFFFFF;
  --color-gray: #E5E5E5;
  --color-gray-darker: #999999;
  --color-blue: #0065FF;
}

.success-stories-wrapper {
  display: flex;
  justify-content: space-between;
}
.success-stories-wrapper .subtitle {
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: var(--color-orange);
  margin-bottom: 10px;
  font-weight: bold;
  flex: 0 0 auto;
}

.featured-story {
  display: flex;
  flex-direction: column;
}
.featured-story > div {
  display: flex;
  flex: 1;
}
.featured-story .views-row {
  display: flex;
}
.featured-story .node {
  display: flex;
  flex-direction: column;
}

.success-stories {
  display: flex;
  flex-direction: column;
}
.success-stories h3 {
  text-align: center;
}